import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import axios from 'axios'
import FloatingBtn from '../components/global/FloatingBtn'
import InspectionList from '../components/inspection/InspectionList'
import UserTemplate from '../template/UserTemplate'
import { setRampInspection } from '../store/ramp-inspection'

function User() {
  const dispatch = useDispatch()
  // flag kalau sudah load ramp inspection
  const [isLoadRampInspection, setIsLoadRampInspection] = useState(false)
  // flag kalau sudah call api
  const [isCallApiRamp, setIsCallApiRamp] = useState(false)
  // indikator loading
  const [isLoadingRamp, setIsLoadingRamp] = useState(false)

  const dummy = useSelector((state) => state.auth.dummy)
  const currentRole = useSelector((state) => state.auth.currentRole)

  const inspection = useSelector((state) => state.rampInspection.rampInspection)
  const [inspectionFiltered, setInspectionFiltered] = useState([])

  const [filter, setFilter] = useState({})

  const loadRamp = () => {
    setIsLoadingRamp(true)
    axios.get(`${axios.defaults.baseURL}/api/ramp`).then((res) => {
      setIsLoadingRamp(false)
      setIsLoadRampInspection(true)
      if (res.data.status) {
        dispatch(setRampInspection(res.data.data))
      } else {
        //
      }
    })
      .catch((err) => {
        setIsLoadingRamp(false)
        setIsLoadRampInspection(true)
        console.log(err)
      })
  }

  const filterRampInspection = () => {
    let filtered = inspection
    if ('q' in filter && filter.q !== '') {
      filtered = filtered.filter((e) => e.aircraft_model.toLowerCase().includes(filter.q)
      || e.operator.toLowerCase().includes(filter.q))
    }
    setInspectionFiltered(filtered)
  }

  const handleSearch = (e) => {
    const search = e.target.value
    setFilter((values) => ({ ...values, q: search }))
  }

  useEffect(() => {
    if (!dummy && !isLoadRampInspection && !isCallApiRamp) {
      setIsCallApiRamp(true)
      loadRamp()
    }
    if (
      (!dummy && isLoadRampInspection)
      || dummy
    ) {
      filterRampInspection()
    }
  })

  return (
    <UserTemplate>
      <div className="px-4 pt-16 pb-40">
        {/* header */}
        <div className="text-3xl text-gray-700 dark:text-gray-300 text-center">
          <h1>Ramp Inspection Checklist</h1>
        </div>
        {/* <div className="mt-8 p-4 bg-brand-100  border border-brand-200 rounded-lg">
          <div>
            <span className="block mt-4">Jumlah data</span>
            <span
              className="block font-medium text-brand-600"
            >
              3
            </span>
          </div>

        </div> */}

        <div
          className="mt-16"
        >
          <input
            type="text"
            placeholder="Search"
            className="t-form"
            value={filter.q || ''}
            onChange={handleSearch}
            required
          />
        </div>
        <div
          className="mt-4"
        >
          {
            (!isLoadingRamp) ? (
              <InspectionList
                inspection={inspectionFiltered}
              />
            ) : (
              <div className="text-center dark:text-gray-300 text-sm">
                Loading...
              </div>
            )
          }
        </div>
        {
          (currentRole.role_slug === 'inspector') ? (
            <Link
              to="/create"
              className="fixed z-100 bottom-24 right-4"
            >
              <FloatingBtn />
            </Link>
          ) : ''
        }
      </div>
    </UserTemplate>
  )
}

export default User
